      var reader = new FileReader()
      // console.log(reader.onload)
      reader.onload = (data) => {
       
        let res = data.target || data.srcElement
 console.log(res)
        this.userInfo.avatar = res.result
        localStorage.img=this.userInfo.avatar
      }
      reader.readAsDataURL(file)
      // console.log(reader)
      
    },

    },
    mounted(){
      if(localStorage.img){
          this.userInfo.avatar=localStorage.img
         }
